import { SearchStatus } from './searchstatus';

export class SearchLog {

    constructor(searchLog?: SearchLog) {

        if (searchLog) {
            for (let prop in searchLog) {
                if (searchLog.hasOwnProperty(prop)) {
                    this[prop] = searchLog[prop];
                }
            }

            this.spIds = [];
            if (searchLog.spIds) {
                for (let spId of searchLog.spIds) {
                    this.spIds.push(new String(spId));
                }
            }
            
        } else {
            this.spIds = [];
            this.spIds.push(new String());
        }
        
    }

    searchId: String;

    keyWord: String;

    zipcode: String;

    searchStatus: SearchStatus;

    totalProductsToSearch: number;

    totalProductsSearched: number;

    message: String;

    spIds: String[];

    percentageCompleted: number;

}