import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductSearchComponent } from './product-search/product-search.component';
import { ProductSearchBoxComponent } from './product-search-box/product-search-box.component';
const routes: Routes = [

  { path: 'search/:searchtype', component: ProductSearchComponent },
  { path: 'search-box/:searchtype', component: ProductSearchBoxComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductsRoutingModule { }